.contact-us {
  margin: 0 auto;
  padding-top: 75px;
  padding-bottom: 32px;
  position: relative; }
  .contact-us::before {
    content: "";
    display: block;
    width: 62px;
    height: 62px;
    position: absolute;
    top: 5px;
    left: 50%;
    transform: translateX(-50%);
    background: url("../../img/site/icon-envelop.png");
    background-repeat: no-repeat;
    background-size: contain; }
  .contact-us .msg {
    padding: 12px 30px 10px;
    margin: 0 -26px;
    margin-bottom: 16px; }
    .contact-us .msg p {
      font-size: 12px; }
  .contact-us .modal__close {
    top: 40px; }
  .contact-us__overlay {
    overflow: auto; }
    @media screen and (max-width: 47.9375em) {
      .contact-us__overlay {
        display: none !important; } }
  .contact-us__title {
    font-size: 25px; }
  .contact-us__form-container {
    padding: 0 26px;
    font-size: 12px; }
  .contact-us__form-field {
    margin-bottom: 14px; }
    .contact-us__form-field input,
    .contact-us__form-field textarea {
      margin-bottom: 0; }
    .contact-us__form-field textarea {
      min-height: 93px; }
  .contact-us__form-captcha {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .contact-us__form-captcha-input, .contact-us__form-captcha-img {
      width: 50%; }
    .contact-us__form-captcha-img {
      text-align: center;
      min-height: 35px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain; }
      .contact-us__form-captcha-img img {
        display: inline-block; }
  .contact-us__form-submit {
    width: 50%;
    padding-top: 11px;
    padding-bottom: 11px; }
  @media screen and (min-width: 48em) {
    .contact-us {
      width: 510px;
      margin: 40px auto;
      padding: 0;
      padding-top: 94px;
      padding-bottom: 50px;
      top: calc(50% - 320px); }
      .contact-us::before {
        width: 81px;
        height: 81px;
        top: 0; }
      .contact-us::after {
        position: absolute;
        content: "";
        display: block;
        background: #fff;
        background-image: repeating-linear-gradient(-45deg, #fff, #fff 8px, #FBB931 8px, #FBB931 16px, #fff 16px, #fff 24px, #01B8A2 24px, #01B8A2 32px, #fff 32px);
        background-repeat: repeat-y;
        background-size: 18px 100%;
        bottom: 0;
        left: 0;
        right: 0;
        top: 40px;
        z-index: -1; }
      .contact-us .msg {
        margin-left: -62px;
        margin-right: -80px;
        padding-left: 62px; }
        .contact-us .msg__close {
          top: 3px; }
      .contact-us__form-container {
        padding-left: 80px;
        padding-right: 80px; }
      .contact-us__form-field {
        margin-bottom: 17px; }
      .contact-us__form-msg {
        margin-right: -80px;
        margin-left: -62px; }
        .contact-us__form-msg p {
          padding-left: 62px;
          padding-right: 80px; } }
